@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
    background-color: var(--white);
    height: 6.5rem;
    line-height: 6.5rem;
  }

  .search{
    display: inline-block;
  }
  .search input{
    border: none;
    border-bottom: 1px solid #ff4545;
  }
  .hide_search{
    display: none;
  }
  
  .nav.fix-nav {
    width: 100%;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    z-index: 999;
  }
  
  nav .wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
  }
  
  .wrapper .logo a {
    color: #ff4545;
    font-size: 2.7rem;
    font-weight: 600;
    padding: 0.5rem;
    font-family: 'Lobster';
    text-shadow: 0.5rem 0.5rem 1rem #b8a1a1;
  }
  
  .wrapper .nav-list {
    display: inline-flex;
  }
  
  .nav-list li i {
    color: var(--grey2);
  }
  
  .nav-list a {
    color: var(--black);
    font-size: 1.5rem;
    padding: 0.9rem 1.5rem;
    border-radius: 0.5rem;
    transition: all 300ms ease;
  }
  
  .nav-list li a:hover {
    color: #ff4545;
  }

  .nav-list li .active{
    color: #ff4545;
  }
  
  .nav-list .drop-menu1,
  .nav-list .drop-menu2 {
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.1);
    width: 20rem;
    top: 8.5rem;
    line-height: 4.5rem;
    position: absolute;
    background-color: var(--white);
    opacity: 0;
    visibility: hidden;
    transition: all 300ms ease;
  }
  
  .nav-list .drop-menu1 li a,
  .nav-list .drop-menu2 li a {
    display: block;
    font-size: 1.5rem;
    width: 100%;
    padding: 0 0 0 1.5rem;
    border-radius: 0;
    color: var(--grey2);
  }
  
  .nav-list .drop-menu1 li a:hover,
  .nav-list .drop-menu2 li a:hover {
    color: #ff4545;
  }
  
  .nav-list li:hover .drop-menu1,
  .nav-list li:hover .drop-menu2,
  .nav-list li:hover .mega-box {
    opacity: 1;
    visibility: visible;
    top: 6.5rem;
  }
  
  .mega-box {
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.1);
    position: absolute;
    top: 8.5rem;
    left: 0;
    width: 100%;
    padding: 0 3rem;
    background-color: var(--white);
    opacity: 0;
    visibility: hidden;
    transition: all 300ms ease;
  }
  
  .mega-box .content {
    padding: 2.5rem 2rem;
    display: flex;
    width: 100%;
    justify-content: space-between;
  }
  
  .mega-box .content .row {
    width: calc(25% - 3rem);
    line-height: 4.5rem;
  }
  
  .content .row img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .content .row header {
    font-size: 1.6rem;
    font-weight: 700;
    color: var(--grey1);
  }
  
  .content .row .mega-links {
    border-left: 1px solid rgba(255, 255, 255, 0.09);
    margin-left: -4rem;
  }
  
  .content .row li {
    padding: 0 2rem;
  }
  
  .content .row li a {
    padding: 0 2rem;
    display: block;
    font-size: 1.5rem;
    color: var(--grey2);
  }
  
  .content .row li a:hover {
    color: #ff4545;
  }
  
  .nav-list .mobile-item {
    display: none;
  }
  
  .nav-list input {
    display: none;
  }
  
  .wrapper .btn {
    color: var(--white);
    font-size: 2rem;
    cursor: pointer;
    display: none;
  }
  
  .nav-list .icons span {
    display: inline-block;
    margin: 0.5rem 1rem 0 0;
    position: relative;
  }
  
  .nav-list .icons .count {
    justify-content: center;
    position: absolute;
    top: -2rem;
    right: -0.8rem;
    background-color: white;
    height: 2rem;
    padding: 0.5rem;
    color: #ff4545;
    font-weight: 700;
    border-radius: 50%;
  }

  
  @media only screen and (max-width: 968px) {
    .wrapper .nav-list{
      position: fixed;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      max-width: 35rem;
      background-color: var(--white);
      display: none;
      overflow-y: auto;
      line-height: 5rem;
      box-shadow: 0 1.5rem 1.5rem rgba(0, 0, 0, 0.1);
      transition: all 300ms linear;
  }

  .nav-list li {
      margin: 1.5rem 1rem;
  }

  .nav-list a{
      display: block;
      font-size: 1.8rem;
      padding: 0 2rem;
      color: var(--black);
  }

  .nav-list .drop-menu1,
  .nav-list .drop-menu2{
      position: static;
      opacity: 1;
      visibility: visible;
      top: 6.5rem;
      padding-left: 2rem;
      width: 100%;
      /* max-height: 0;
      overflow: hidden; */
  }
#showdrop1:checked ~ .drop-menu1,
#showdrop2:checked ~ .drop-menu2{
  max-height: 100%;
}


.nav-list .drop-menu1 li,
.nav-list .drop-menu2 li{
  margin: 0;
}

.nav-list .drop-menu1 li a,
.nav-list .drop-menu2 li a{
  color: var(--grey2);
  font-size: 1.5rem;
}

.nav-list .desktop-item {
  display: none;
}

.nav-list .mobile-item{
  display: block;
  font-size: 1.8rem;
  padding-left: 2rem;
  color: var(--black);
  cursor: pointer;
  border-radius: 0.5rem;
  transition: all 300ms ease;
}

.nav-list .mobile-item:hover{
  color: var(--primary);
}

.mega-box{
  position: static;
  top: 6.5rem;
  padding: 0 2rem;
  opacity: 1;
  visibility: visible;
  /* max-height: 0;
  overflow: hidden; */
  transition: all 300ms ease;
}

.mega-box .content {
  flex-direction: column;
  padding: 2rem 2rem 0 2rem;
}

.mega-box .content .row{
  width: 100%;
  border-top: 1px solid rgba(255, 255, 255, 0.09);
  margin-bottom: 1.5rem;
}

.content .row:nth-child(1),
.content .row:nth-child(2) {
  border-top: 0;
}

.content .row .mega-links {
  border-left: 0;
  padding-left: 2rem;

    }
  
    .wrapper .btn {
      display: block;
      color: var(--black);
    }
  
    .nav-list .top {
      position: relative;
      display: block;
      background-color: #ff4545;
      width: 100%;
      height: 8rem;
    }
  
    .nav-list .close-btn {
      position: absolute;
      top: 1.5rem;
      right: 2.5rem;
      color: var(--white);
    }
  
    .nav-list .icons {
      padding-left: 2rem;
    }
  }